.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-section {
  min-height: 80vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Img-container {
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
}
.App-link {
  color: #61dafb;
}

.radio {
  margin: 5px
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
